}

.loader1:after {
  background: #fd3e3e;
  animation-delay: 0.4s;
}

@keyframes revolve {
  0% {
    left: 0;
  }
  25% {
    transform: scale(calc(1 + 0.2));
  }
  50% {
    left: 50px;
  }
  75% {
    transform: scale(calc(1 - 0.2));
  }
  100% {
    left: 0;
  }
}
